@import 'fonts';

.page-ksf {
    position: relative;
    overflow: hidden;
    min-height: 100vh;
    margin-top: 54px;

    .button-back {
        padding-left: 16px;
    }

    .img-bg {
        position: absolute;
        right: 0;
        bottom: 0;
    }

    .box-content {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 100%;
        z-index: 2;

        h3 {
            font-family: SF Pro Text;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 1.25;
            /* identical to box height, or 125% */
            /* Black/100 */
            color: #333333;
            margin-top: 15px;
            margin-bottom: 5px;
        }

        p {
            font-family: SF Pro Text;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 1.43;
            /* identical to box height, or 143% */
            color: #333333;
            margin-bottom: 12px;
        }

        a {
            font-family: SF Pro Text;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 1.43;
            color: #228BCC;
        }

        .tim-hieu-them {
            margin-bottom: 29px;
        }

        .link-app {
            display: inline-block;
        }
    }

    .content-bottom {
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        z-index: 2;
        text-align: center;
        font-family: SF Pro Text;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 1.33;
        /* or 133% */
        color: rgba(51, 51, 51, 0.8);

        p {
            margin-bottom: 5px;
        }

        a {
            color: #228BCC;
        }
    }
}